	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>本日の成績: 4,286円 → 1,210円</strong> ここまで吹っ飛ぶと、なんかある意味清々しいですね。今日の日銀「マイナス金利」導入はやばかったです。2分で4円近くぶれて全部吹っ飛びました。  ということで、今回は殆ど事故のようなものでしたが、事故とはいえ結果は結果、今からでは月利 4 パーセント間に合わないため、<em>来月の現金追加投入はミニマムの 3,000円決定です</em>。来月は4,200円ちょいからのスタートの予定です。 以下、今回の件を受けて、私の所見です。</p>\n<ul>\n<li>今回のような大規模なぶれが起きたら、そのあとにやってくるトレンドに要注意。</li>\n<li>→実際、最初のぶれで吹っ飛ばされ、<strong>その後のトレンドで絞殺されてとどめ刺された感じ</strong>です。</li>\n<li>ちょっと遅すぎたけど、今回の損切りは正解。このまま塩漬けしていたらロスカット確定コースだった。</li>\n<li>→ポジションのマイナスがクレジットの 1 割を超えたら損切りでもよさそう。</li>\n<li>ロットサイズ積みすぎた、言い換えると欲張りすぎたのも反省点である。</li>\n<li>ルールの穴がまだあるの、本格的にどうにかしないとだめだなこれ……。</li>\n<li>いくら勉強用の余剰金で少額とはいえ、証拠金維持率が 200 パーセント下回った時の心臓締め付けられるような感覚がなかなか……でも、悪くない。\n先日の失敗に続いて、繰り返し言いますが、<strong>これで FX やめるようなまくじゃないぜ！</strong>(Ｍ並感) 失敗した、勉強になった、辞めよう。ではもったいない。 折角詰むパターンを一つ学習できたので、次からは回避策が組めるし。 しかし、それにしても少額実験で本当に良かったｗ おまけ: 事故る直前の、兵どもが夢の跡な戦績もうｐしておきます。 <img src=\"/wp-content/uploads/2016/01/FX20160129_1.png\" alt=\"FX20160129_1\"></li>\n</ul>","frontmatter":{"cover":null,"date":"2016-01-29T21:00:36+09:00","strDate":"2016/1/29","title":"[FX]死亡のお知らせ、再び"}}},"pathContext":{"path":"/ja/blog/2016/01/29-fx死亡のお知らせ、再び/","langKey":"ja"}}

